.sidebar-box {
    /* background-color: var(--primaryColor); */
    background-color: #1a2b3c;
    width: 18%;
    height: 100%;
    padding: 1rem;
    /* color: var(--secondaryTxtColor); */
    color: whitesmoke;
}

.sidebar-heading {
    /* color: var(--primaryTxtColor); */
    color: grey;
}

.sidebar-container {
    display: flex;
    flex-direction: column;
    padding: 0rem .3rem;
    gap: .6rem;
    margin-block: .8rem;
}

.sidebar-container>* {
    display: flex;
    align-items: center;
    gap: 1rem;
    font-size: 80%;
    text-decoration: none;
    color: whitesmoke;
}

.sidebar-container>div>span {
    font-size: 21px !important;
}

.hr {
    margin-block: .8rem;
}

/* -------------------------------RESPONSIVE--------------------------- */
@media all and (max-width: 760px) {
    .sidebar-box {
        width: 21%;
    }
}

@media all and (max-width: 650px) {
    .sidebar-box {
        display: var(--showSideBar);
        width: 24%;
    }
}

@media all and (max-width: 550px) {
    .sidebar-box {
        width: 100%;
    }
}