.faq-list {
    list-style: none;
    margin-bottom: 1rem;
}

.faq-list>input {
    display: none;
}

.faq-list>label {
    font-weight: 700;
    font-size: 1.4rem;
    color: var(--primaryTxtColor);
}

.faq-list>label::before {
    content: '+';
    margin-right: 1rem;
    font-weight: 700;
}

.faq-list>input:checked+label::before {
    content: '-';
}

.faq-list>p {
    margin-top: .5rem;
    color: var(--secondaryTxtColor);
    display: none;
    padding: 0rem 2rem;
}

.faq-list>input:checked+label+p {
    /* height: min-content; */
    display: block;
}